import React from 'react';
import './App.css';

import { BrowserRouter as Switch, Route } from "react-router-dom";

import Header from "../Header/Header"
import Footer from "../Footer/Footer"

import Rate from "../Rate/Rate"
import About from "../About/About"


class App extends React.Component {
  constructor(proops) {
    super(proops);
    this.state = {

    }
  }
  closedCoolie = () => {
    document.querySelector(".well").innerHTML = "";
    localStorage.setItem("coolie", "true");
  }
  render() {
    return (
      <div className="siteContainer">
        < Header />
        <div className="container">
          <main>

            <Switch>
              <Route exact path="/" component={Rate} />
              <Route exact path="/about" component={About} />
            </Switch>

          </main>
        </div>
        {(localStorage.getItem("coolie") === "true") ? "" :
          <div className="well">
            На нашем сайте мы используем cookie для сбора информации технического характера.<br />В частности,для персонифицированной работы сайта мы обрабатываем Ip-адрес региона вашего местоположения. &nbsp;
              <button onClick={this.closedCoolie} className="btn btn-primary btn-sm">Ok</button>
          </div>}



        <Footer />

      </div>




    )
  }
}

export default App;
