import React from 'react';
import './About.css';



class About extends React.Component {

  render() {
    let adress = { Country: "Russia", City: "Moscow", Street: "....", House: "....", Tel: '899999999', Index: '00000' }
    return (
      <div className="aboutCss">
        <h1>Contacts:</h1>
        <div style={{ marginLeft: "5%" }}>
          {Object.keys(adress).map(index => <h3 key={index}>{index} : {adress[index]}</h3>)}
        </div>

      </div>
    )
  }
}

export default About;
